@import '/styles.scss';

body {
    font-family: "Montserrat", sans-serif;
    background-color: #e5e5e5;
}

.scelet {
    /* position: absolute; */
    width: 1200px;
    height: 1319px;
    margin-left: auto;
    margin-right: auto;
}

/* main styles */
.main {
    display: flex;
    flex-direction: row-reverse;
}

.main_content {
    /* margin: 0 auto; */
    background-color: #ffffff;
    /* width: 1200px; */
    box-shadow: 6px 6px 20px rgba(0, 0, 0, 0.1);
    padding: 90px 80px;
    padding-left: 40px;
    padding-bottom: 150px;
}


.about_position {
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 17px;
    color: #000000;
    margin-bottom: 20px;
}

.about_name {
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 55px;
    color: #000000;
    margin-bottom: 10px;
    margin-top: 0;
}

.about_discribtion {
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 30px;
    color: #595959;
    /* width: 600px; */
}

/* project styles */
.projects {
    margin-bottom: 50px;
    /* background-color: yellowgreen;
    margin-top: 100px;
    margin-bottom: 300px;
    margin-left: 60px;
    margin-right: 90px; */
    /* margin: 100px 90px 300px 60px;
    margin: 50px;
    margin: 100px 50px;  */
}

.title {
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 27px;
    color: #000000;
    margin-bottom: 20px;
    margin-top: 50px;
    /* font-size: 45px;
    color: teal; */
}

.projects_list {
    padding: 0;
    margin: left 15px;
    /* list-style: none; */
}

.projects_item {
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 1.71;
    color: #000000;
    margin-left: 15px;
}

.project,
.project_link {
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 1.71;
    color: #595959;
}

.project {
    display: flex;
    justify-content: space-between;
}

.project-name {
    margin: 0;
}

.projects_link {
    margin-left: 10px;
}

.project_bracket {
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 1.71;
    color: #000000;
}


.work_position {
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 20px;
    color: #000000;
    margin-bottom: 10px;
}

.organization {
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 20px;
    color: #fb6d3a;
}

.period {
    font-style: normal;
    font-weight: normal;
    /* font-size: 18px;
  line-height: 15px; */
    color: #453535;
    margin-bottom: 10px;
}

.line {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 15px;
    color: #000;
}

.work_list {
    padding: 0;
    margin-left: 15px;
}

.work_list_item {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color: #595959;
}

/* education styles  */
.education {
    font-style: normal;
